:root {
  box-sizing: border-box;
  font-family: sans-serif;
}

.gallery {
  max-width: 1200px;
  flex-wrap: wrap;
  margin: 20px auto;
  display: flex;
}

.gallery .photo-card {
  width: calc(25% - 20px);
  margin: 10px;
  overflow: hidden;
  box-shadow: 1px 1px 5px #999;
}

.gallery .photo-card .image {
  height: 240px;
  position: relative;
  overflow: hidden;
}

.gallery .photo-card img {
  width: auto;
  height: 100%;
  display: block;
}

.gallery .photo-card .info {
  width: 100%;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  display: flex;
}

.gallery .photo-card .info .info-item {
  width: 25%;
  text-align: center;
  flex: 25%;
  padding: 5px;
}

.gallery .photo-card .info .info-item b {
  display: block;
}

#search-form {
  text-align: center;
  background-color: #9abed8;
  padding: 20px;
}

#search-form input, #search-form button {
  height: 40px;
  max-height: 40px;
  box-sizing: border-box;
  padding: 8px 16px;
}

